import React, { useCallback, useEffect, useState } from "react";
import {
  View,
  Button,
  Swiper,
  SwiperItem,
  Image,
  ScrollView,
  Text
} from "@tarojs/components";
import Taro, {
  useShareAppMessage,
  useReady,
  request,
  useDidShow,
  usePullDownRefresh,
  useReachBottom,
  redirectTo
} from "@tarojs/taro";
import "./index.scss";
import classes from "./index.module.scss";
import classnames from "classnames";

function Index() {
  useShareAppMessage(() => ({}));
  useReady(() => {
    requestList();
  });
  const [datalist, setDatalist] = useState([]);
  const redirectDetail = (item)=>{
    Taro.setStorage({
      key: 'id',
      data: item.id
    })
    Taro.navigateTo({ url: "../detail/index?id="+item.id})
  }
  const requestList = () => {
    Taro.showLoading({
      title: "加载中"
    });
    setTimeout(function() {
      Taro.hideLoading();
    }, 5000);
    Taro.request({
      url: "https://xcf.haozii.com/wp-json/wp/v2/posts?categories=20&page=1",
      data: {
      },
      success: function(res) {
        Taro.hideLoading();
        setDatalist(res.data);
        console.log(res.data);
      },
      fail: function() {
        Taro.hideLoading();
      }
    });
  };
  return (
    <View className={classes.page}>
      <View className={classes.items}  >
        {datalist.map((item, i) => (
          <View className={classes.top_banner} onClick={()=>redirectDetail(item)}>
            <Image
              src={item.meta.thumbnaill}
              className={classes.top_banner_img}
              mode="aspectFill"
            />
            <View className={classes.textbg}>{item.title.rendered}</View>
          </View>
        ))}
      </View>
    </View>
  );
}
export default Index;
